.gflow-common {

	&.admin-bar {

		.gform-flyout {

			&.gform-flyout--fixed {
				top: 46px;

				@media (--viewport-wpadmin) {
					top: 32px;
				}
			}
		}
	}

	.gform-flyout {
		background: var(--c-white);
		bottom: 0;
		box-shadow: var(--bs-popup);
		box-sizing: border-box;
		display: none;
		flex-direction: column;
		height: 100%;
		left: 0;
		opacity: 0;
		padding-top: 1.25rem;
		position: absolute;
		top: 0;
		transition: opacity 150ms ease-in-out, width 150ms ease-in-out;

		&:hover {

			.gform-flyout__expand {
				opacity: 1;
			}
		}

		p {
			margin: 0;
			padding: 0;
		}

		&.gform-flyout--anim-in-ready {
			display: flex;
		}

		&.gform-flyout--anim-in-active {
			opacity: 1;
		}

		&.gform-flyout--fixed {
			position: fixed;
		}

		&.gform-flyout--absolute {
			position: absolute;
		}

		&.gform-flyout--hide-expander {

			.gform-flyout__expand {
				opacity: 0;
			}
		}

		&.gform-flyout--right {
			left: auto;
			right: 0;

			.gform-flyout__expand-rail {
				left: -35px;
			}

			.gform-flyout__expand {
				left: -35px;
			}

			.gform-flyout__expand-icon {
				transform: rotate(90deg);
			}
		}

		&.gform-flyout--left {

			.gform-flyout__expand-rail {
				right: -35px;
			}

			.gform-flyout__expand {
				right: -35px;
			}

			.gform-flyout__expand-icon {
				transform: rotate(-90deg);
			}
		}

		&.gform-flyout--expanded {

			&.gform-flyout--right {

				.gform-flyout__expand-icon {
					transform: rotate(-90deg);
				}
			}

			&.gform-flyout--left {

				.gform-flyout__expand-icon {
					transform: rotate(90deg);
				}
			}
		}

		.gform-flyout__close.gform-button--size-xs {
			align-items: center;
			border: 1px solid var(--c-snuff);
			display: flex;
			font-size: 1.25rem;
			height: 1.625rem;
			position: absolute;
			right: 25px;
			top: 15px;
			transition: var(--tr-cl-button-focus);
			width: 1.625rem;
			z-index: 5;

			&:hover,
			&:focus {
				border-color: var(--c-gravity-blue);
			}

			.gform-button__icon {
				font-size: 1.875rem;
				top: 0;
			}
		}

		.gform-flyout__expand-rail {
			height: 56px;
			margin-top: -28px;
			position: absolute;
			top: 50%;
			width: 35px;
		}

		.gform-flyout__expand {
			align-items: center;
			background: var(--c-white);
			border: 1px solid var(--c-white-lilac);
			border-radius: 19px;
			box-shadow: var(--bs-popup);
			color: var(--c-port);
			cursor: pointer;
			display: flex;
			height: 56px;
			justify-content: center;
			margin-top: -28px;
			opacity: 0;
			position: absolute;
			top: 50%;
			transition: opacity 0.15s ease, transform 300ms ease, box-shadow 300ms ease, border-color 300ms ease;
			width: 19px;

			&:focus {
				border-color: var(--c-snuff);
				opacity: 1;
				outline: none;

				.gform-flyout__expand-icon {
					color: var(--c-gravity-blue);
				}
			}

			&:hover {

				.gform-flyout__expand-icon {
					color: var(--c-gravity-blue);
				}
			}
		}

		.gform-flyout__expand-icon {
			align-items: center;
			color: var(--c-port);
			display: flex;
			font-size: 1rem;
			height: 15px;
			justify-content: center;
			position: relative;
			transition: color 300ms ease;
			width: 100%;
		}
	}

	.gform-flyout__head {
		padding: 0 1.5625rem 1.25rem;
		position: relative;

		&::after {
			background: var(--c-white-lilac);
			bottom: 0;
			content: "";
			height: 0.0625rem;
			left: 0;
			position: absolute;
			width: 100%;

			.gform-flyout--no-divider & {
				display: none;
			}
		}
	}

	.gform-flyout__title {
		color: var(--c-port);
		font-family: var(--t-font-family-base);
		font-size: var(--t-font-size-base);
		font-weight: var(--t-font-weight-medium);
		line-height: 18px;
		margin: 0 0 12px;
	}

	.gform-flyout--no-description {

		.gform-flyout__title {
			margin: 0;
		}
	}

	.gform-flyout__desc {
		color: var(--c-port);
		font-family: var(--t-font-family-base);
		font-size: var(--t-font-size-base);
		padding-right: 1.125rem;
	}

	.gform-flyout__body {
		color: var(--c-port);
		font-family: var(--t-font-family-base);
		font-size: var(--t-font-size-base);
		overflow-y: auto;
		padding: 1.25rem 1.5625rem;

		&:focus-within {

			.gform-flyout__expand {
				opacity: 1;
			}
		}

		.simplebar-wrapper {
			height: 100%;
		}
	}
}

html[dir="rtl"] .gflow-common {

	.gform-flyout {
		left: auto;
		right: 0;

		&.gform-flyout--right {
			left: 0;
			right: auto;

			.gform-flyout__expand-rail {
				left: auto;
				right: -35px;
			}

			.gform-flyout__expand {
				left: auto;
				right: -35px;
			}

			.gform-flyout__expand-icon {
				transform: rotate(-90deg);
			}
		}

		.gform-flyout__close.gform-button--size-xs {
			left: 25px;
			right: auto;
		}

		&.gform-flyout--left {

			.gform-flyout__expand-rail {
				left: -35px;
				right: auto;
			}

			.gform-flyout__expand {
				left: -35px;
				right: auto;
			}

			.gform-flyout__expand-icon {
				transform: rotate(90deg);
			}
		}

		&.gform-flyout--expanded {

			&.gform-flyout--right {

				.gform-flyout__expand-icon {
					transform: rotate(90deg);
				}
			}

			&.gform-flyout--left {

				.gform-flyout__expand-icon {
					transform: rotate(-90deg);
				}
			}
		}
	}

	.gform-flyout__desc {
		padding-left: 1.125rem;
		padding-right: 0;
	}
}
