.gflow-common {

	.gform-input {
		-webkit-appearance: none;
		background: var(--c-white);
		border: 1px solid var(--c-blue-haze);
		border-radius: 3px;
		box-shadow: 0 0 0 transparent;
		box-sizing: border-box;
		color: var(--c-text);
		font-family: var(--t-font-family-base);
		font-size: var(--t-font-size-base);
		line-height: 2;
		outline: none;
		padding: 0.25rem 0.75rem;
		transition: var(--tr-inputs);

		&:focus {

			@mixin inputFocus;
		}

		&::placeholder {
			color: var(--c-comet);
			font-family: var(--t-font-family-base);
			font-size: var(--t-font-size-base);
			font-weight: var(--t-font-weight-normal);
		}
	}

	.gform-input--radio,
	.gform-input--checkbox {
		border: 0 none;
		clip: rect(0.125rem, 0.125rem, 0.125rem, 0.125rem);
		-webkit-clip-path: inset(50%);
		clip-path: inset(50%);
		height: 0.0625rem;
		margin: -0.125rem;
		overflow: hidden;
		padding: 0;
		position: absolute;
		width: 0.0625rem;
	}

	.gform-input--radio {

		+ .gform-input__label--radio {

			&::before {
				background-color: var(--c-white);
				border: 1px solid var(--c-amethyst-smoke);
				border-radius: 50%;
				content: "";
				display: inline-block;
				height: 1.25rem;
				margin-right: 0.625rem;
				top: 1.5625rem;
				transition: var(--tr-inputs);
				vertical-align: middle;
				width: 1.25rem;
			}

			&:hover {

				&::before {
					background-image: url("data:image/svg+xml,%3Csvg width='8' height='8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M4 8a4 4 0 1 0 0-8 4 4 0 0 0 0 8Z' fill='%23ECEDF8'/%3E%3C/svg%3E"); /* stylelint-disable-line */
					background-position: center;
					background-repeat: no-repeat;
					border-color: var(--c-primary-light);
					box-shadow: var(--bs-focus);
					display: inline-block;
				}
			}
		}

		&:checked {

			+ .gform-input__label--radio {

				&::before {
					background-color: var(--c-white);
					background-image: url("data:image/svg+xml,%3Csvg width='8' height='8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M4 8a4 4 0 1 0 0-8 4 4 0 0 0 0 8Z' fill='%233E7DA6'/%3E%3C/svg%3E"); /* stylelint-disable-line */
					background-position: center;
					background-repeat: no-repeat;
					border-color: var(--c-amethyst-smoke);
				}
			}

			&:focus {

				+ .gform-input__label--radio {

					&::before {
						box-shadow: var(--bs-focus);
					}
				}
			}
		}
	}

	.gform-input__label {
		align-items: center;
		color: var(--c-port);
		cursor: pointer;
		display: inline-flex;
		font-family: var(--t-font-family-base);
		font-size: var(--t-font-size-small);
		margin-right: 0.3rem;
		outline: none;
	}

}

html[dir="rtl"] .gflow-common {

	.gform-input--radio {

		+ .gform-input__label--radio {

			&::before {
				margin-left: 0.625rem;
				margin-right: 0;
			}
		}
	}
}
